@font-face {
  font-family: 'Roboto';
  src: local('Roboto Regular'), local('Roboto-Regular'),
       url('/fonts/Roboto-Regular-webfont.woff') format('woff');
  font-weight: 400; // Normal weight.
  font-style: normal;
}

@font-face {
  font-family: 'Roboto';
  src: local('Roboto Italic'), local('Roboto-Italic'),
       url('/fonts/Roboto-Italic-webfont.woff') format('woff');
  font-weight: 400; // Normal weight.
  font-style: italic;
}

@font-face {
  font-family: 'Roboto';
  src: local('Roboto Medium'), local('Roboto-Medium'),
       url('/fonts/Roboto-Medium-webfont.woff') format('woff');
  font-weight: 500; // Medium weight.
  font-style: normal;
}

@font-face {
  font-family: 'Roboto';
  src: local('Roboto Bold'), local('Roboto-Bold'),
       url('/fonts/Roboto-Bold-webfont.woff') format('woff');
  font-weight: 700; // Bold weight.
  font-style: normal;
}

* {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
img {
  max-width: 100%;
}
a {
  text-decoration: none;
  outline: none;
  color: #444;
}
a:hover {
  color: #444;
}
a:hover, a:focus, input, textarea {
  text-decoration: none;
  outline: none;
}
.center {
  text-align: center;
}
.left {
  text-align: left;
}
.right {
  text-align: right;
}
.cp {
  cursor: pointer;
}
html, body {
  height: 100%;
}
p {
  margin-bottom: 0px;
  width: 100%;
}
.no-padding {
  padding: 0px;
}
.no-margin {
  margin: 0px;
}
.hid {
  display: none;
}
.top-mar {
  margin-top: 15px;
}
.h-100 {
  height: 100%;
}
::placeholder {
  color: #747f8a !important;
  font-size: 13px;
  opacity: .5 !important;
}
.container-fluid {
  padding: 0px;
}
h1, h2, h3, h4, h5, h6 {
  font-family: 'Roboto', sans-serif;
}
h2 {
  font-weight: 500;
}
h5{
  font-weight: 500;
  font-size: .9rem;
}
h6 {
  font-weight: 500;
  color: #009b7f;
  margin-bottom: 0px;
  width: 100%;
  font-size: .9rem;
}
strong {
  font-weight: 500;
  color: #4f4f4f;
  font-size: inherit;
  font-family: inherit;
}

.highlight{
  color: #009b7f;
  font-weight: 500;
}
body {
  background-color: #f7f7ff !important;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  color: #6A6A6A;
}
.session-title {
  padding: 30px;
  margin: 0px;
}
.session-title h2 {
  width: 100%;
  text-align: center;
}
.session-title p {
  max-width: 850px;
  text-align: center;
  float: none;
  margin: auto;
}
.session-title span {
  float: right;
  font-style: italic;
}
.inner-title {
  padding: 20px;
  padding-left: 0px;
  margin-bottom: 30px;
}
.inner-title h2 {
  width: 100%;
  text-align: center;
  font-size: 2rem;
}
.inner-title p {
  width: 100%;
  text-align: center;
}
.page-nav {
  padding: 40px;
  text-align: center;
  padding-top: 160px;
}
.page-nav h2 {
  font-size: 36px;
  width: 100%;
  color: #444;
}

.btn-success {
  background-color: #00ab9f;
  border-color: #00ab9f;
}
.btn-success:hover {
  background-color: #00ab9f !important;
  border-color: #00ab9f !important;
}
.btn-success:active {
  background-color: #00ab9f !important;
  border-color: #00ab9f !important;
}
.btn-success:focus {
  background-color: #00ab9f !important;
  border-color: #00ab9f !important;
  box-shadow: none !important;
}
.btn-info {
  background-color: #4f6dcd;
  border-color: #4f6dcd;
}
.btn-info:hover {
  background-color: #4f6dcd !important;
  border-color: #4f6dcd !important;
}
.btn-info:active {
  background-color: #4f6dcd !important;
  border-color: #4f6dcd !important;
}
.btn-info:focus {
  background-color: #4f6dcd !important;
  border-color: #4f6dcd !important;
  box-shadow: none !important;
}
.btn {
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  border-radius: 2px;
}
.form-control:focus {
  box-shadow: none !important;
  border: 2px solid #00a8df;
}
.btn-light {
  background-color: #FFF;
  color: #3F3F3F;
}
.collapse.show {
  display: block !important;
}
.form-control:focus {
  box-shadow: none;
  border: 2px solid #0d7a40 !important;
}
.form-control {
  background-color: #F8F8F8;
  margin-bottom: 20px;
}
.form-control:focus {
  background-color: #FFF;
  border-color: #CCC;
}
.container {
  max-width: 1100px;
}
/* ===================================== Header CSS ================================== */
.profile-container {
  max-width: 1100px;
  margin-top: 20px;
  padding-bottom: 10px;
}
.profile-container .pro-s-cover {
  background-color: #FFF;
  padding: 20px;
  text-align: center;
  box-shadow: 0 2px 6px 0 rgba(218, 218, 253, 0.65), 0 2px 6px 0 rgba(206, 206, 238, 0.54);
  margin-bottom: 20px;
}
.profile-container .pro-s-cover img {
  border-radius: 50px;
}
.name {
  font-weight: 500;
  font-size: 1.2rem;
  margin-top: 10px;
}
.profile-container .pro-s-cover span {
  font-size: .9rem;
}
.profile-container .pro-s-cover p {
  font-size: .9rem;
  color: #009b7f;
  font-weight: 600;
}
.profile-container .pro-s-cover .num-box {
  width: 100%;
  border-top: 1px solid #CCC;
  margin-top: 10px;
}
.profile-container .pro-s-cover .num-box .hire {
  flex: 1;
  text-align: center;
  border-right: 1px solid #CCC;
  padding: 10px 0px;
}
.profile-container .pro-s-cover .num-box .hire span {
  font-size: .9rem;
}
.profile-container .pro-s-cover .num-box .msg {
  flex: 1;
  text-align: center;
  padding: 10px 0px;
}
.profile-container .pro-s-cover .num-box .msg span {
  font-size: .9rem;
}
.profile-container .pro-s-cover .msg-box {
  width: 100%;
  padding-top: 20px;
  border-top: 1px solid #CCC;
}
.profile-container .pro-s-cover .msg-box .btn {
  width: 110px;
  box-shadow: none;
}

.profile-container .pro-s-cover .msg-box .hire {
  flex: 1;
  text-align: center;
}
.profile-container .pro-s-cover .msg-box .msg {
  flex: 1;
  text-align: center;
}
.profile-container .con-cover {
  background-color: #FFF;
  padding: 10px;
  box-shadow: 0 2px 6px 0 rgba(218, 218, 253, 0.65), 0 2px 6px 0 rgba(206, 206, 238, 0.54);
  margin-bottom: 20px;
}
.profile-container .con-cover h4 {
  font-size: 1.1rem;
  padding-left: 10px;
  padding-top: 5px;
}
.profile-container .con-cover .cd-ro {
  border-top: 1px solid #CCC;
  padding: 10px;
}
.profile-container .con-cover .cd-ro span {
  font-size: 0.8rem;
}
.profile-container .con-cover .cd-ro p {
  font-size: .8rem;
  margin-top: 6px;
}

.profile-container .con-cover .cd-ro p i {
  font-size: 14px;
  margin-right: 10px;
}
.profile-container .data-box {
  padding: 19px 20px 20px 20px;
  background-color: #FFF;
  box-shadow: 0 2px 6px 0 rgba(218, 218, 253, 0.65), 0 2px 6px 0 rgba(206, 206, 238, 0.54);
  margin-bottom: 20px;
}
.profile-container .data-box h2 {
  font-size: 1.1rem;
  border-bottom: 1px solid #CCC;
  padding-bottom: 10px;
  margin-bottom: 10px;
}
.profile-container .data-box > h6 {
  margin-top: 16px;
  font-size: 0.9rem;
}

.profile-container .data-box p {
  text-indent: 20px;
  text-align: justify;
  font-size: .9rem;
  margin-bottom: 8px;
}

.profile-container .data-box .exp-row {
  margin: 0px;
  margin-bottom: 16px;
}
.profile-container .data-box .exp-row h6 {
  color: #009b7f;
  margin-bottom: 0px;
  width: 100%;
  font-size: .9rem;
}
.profile-container .data-box .exp-row i {
  margin-left: 10px;
  font-size: .9rem;
}
.exp-row *a {
  margin-left: 4px;
  color: #6A6A6A;
  text-decoration: underline;
}
.company-name{
  font-size: .9rem;
  font-weight: 500;
}
.techlabel {
  color: #6A6A6A;
  font-size: .9rem;
  font-weight: 400;
}
.profile-container .data-box .exp-row .outsourcelabel {
  margin-left: 14px;
  font-size: .7rem;
  margin-bottom: 8px;
  font-weight: 500;
}
.profile-container .data-box .exp-row .outsourcelabel span {
  color: #888;
  margin-top: 8px;
  margin-left: 0px;
  font-size: .7rem;
  font-weight: 400;
}
.profile-container .data-box .exp-row p {
  margin-top: 5px;
  text-indent: 15px;
}
.profile-container .data-box .last {
  margin-bottom: 0px;
  padding-bottom: 0px;
  border: 0px;
}
.profile-container .education-detail .sec-title {
  padding: 20px 0px;
  text-align: center;
  font-size: 22px;
}
.profile-container .education-detail .resume-dat {
  text-align: center;
  margin: auto;
}

.profile-container .education-detail .resume-dat p {
  width: 100%;
  text-indent: 0px;
  margin: auto;
  text-align: center;
}
.profile-container .education-detail .resume-dat h6 {
  text-align: center;
}
.profile-container .education-detail .service {
  padding-top: 10px;
}
.profile-container .education-detail .service .resume-dat {
  text-align: center;
}
.profile-container .education-detail .rgbf {
  border-left: 1px solid #CCC;
}
.profile-container .education-detail .rgbf h5 {
  font-size: 1.2rem;
}
.profile-container .service-box .service {
  padding: 10px;
  margin-bottom: 10px;
  background-color: #dadada21;
}
.profile-container .service-box .service .serv-logo {
  margin: auto;
  text-align: center;
  font-size: 44px;
}
.profile-container .service-box .service .serv-logo i {
  margin: auto;
}
.profile-container .service-box .service .serv-det h5 {
  font-size: 1rem;
}
.contact-tab {
  padding: 20px;
}
.contact-tab iframe {
  width: 100%;
  height: 400px;
}
.custom-button-box {
  width: 100%;
  border-top: 1px solid #CCC;
  text-align: center;
}

.contact-a{
  color: #6A6A6A;
}

.contact-container{
  font-size: .9rem;
}

.contact-container * i{
  margin-right: 8px;
}

.contact-container-sm{
}

.contact-container-sm > div{
  margin-top: 8px;
}

.skill-table{
  font-size: 0.9rem;
}

.skill-table >tbody >tr >td{
  border-bottom: 4px solid rgba(0, 0, 0, 0);
}


.contact-table{
  font-size: .9rem;
}

.contact-table >tbody >tr >td{
  border-left: 8px solid rgba(0, 0, 0, 0);
  border-bottom: 8px solid rgba(0, 0, 0, 0);
}

.project-description {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 0.9rem;
  padding-left: 20px;
  margin-bottom: 0px;
  text-align: justify;
}

/* Optional: To style the list items within */
.project-description li {
  list-style-type: disc;
}

.table-techlabel{
  font-size: 0.9rem;
}

.table-techlabel > tbody > tr > td {
  padding-right: 4px;
}

.highlight-link{
  color: #00ab9f!important;
  //text-decoration: underline;
}